.wrapper {
    display: flex;
    align-items: center;
    padding: 6px 16px;
    cursor: pointer;
}
.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.info {
    flex: 1;
    margin-left: 12px;
}
.name {
    font-size: 16px;
    font-weight: 600;
    color: #000;
}

.check {
    margin-left: 6px;
    color: rgb(32, 213, 236);
    font-size: 1.6rem;
}

.username {
    font-size: 1.2rem;
    color: rgba(22, 24, 35, 0.75);
    font-weight: 500;
}
